
import React, { lazy, Suspense } from "react";
import { Redirect } from "react-router-dom";
const SuspenseComponent = Component => props => {
	return (
		<Suspense fallback={null}>
			<Component {...props}></Component>
		</Suspense>
	);
};

const Layout = lazy(() => import("@layouts/BasicLayout"));
const MKToolList = lazy(() => import("@views/marketTools"));
const UserManage = lazy(() => import("@views/userManage"));

const route = [
	{
		path: "/",
		name: 'hahha',
		component: SuspenseComponent(Layout),
		routes: [
			{
				path: "/",
				exact: true,
				meta: {icon: 'UserOutlined' },
				hide: true,
				// component: SuspenseComponent(MKToolList),
				render: () => <Redirect to={"/mk-tool"} />
			},
			{
				path:"/mktools",
				name: '首页',
				component: SuspenseComponent(MKToolList),
			},
			{
				path:"/user",
				name: '用户',
				component: SuspenseComponent(UserManage),
			},
		]
  }
]

export default route